html {
  width: 100%;
  height: 100%;
}
.Loading {
  display: flex;
  text-align: center;
  margin-top: 600px;
  font-size: xx-large;
  color: rgb(101, 106, 111);
}
.Detail {
  margin-top: 100px;
}
.Text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  background: linear-gradient(transparent, rgb(236, 247, 255) 60%);
  height: 100%;
  width: 100%;
}
.Poster {
  width: 200px;
  position: relative;
  margin: auto;
}

.Title {
  color: rgb(21, 21, 21);
  margin: auto;
  width: 50%;
}

.Background {
  width: 100%;
  object-fit: cover;
  z-index: -1;
  position: absolute;
  top: 0%;
  transform: translate(-1%, 0%);
  max-height: 1000px;
  -webkit-mask-image: linear-gradient(
    to bottom,
    #000 0%,
    transparent 80%,
    transparent 100%
  );
}
.Info {
  font-size: large;
  display: flex;
  justify-content: space-around;
  margin: auto;
  margin-bottom: 20px;
  width: 45%;
}
.Imoji {
  font-size: xx-large;
  
}
.Year {
    font-weight: 700;
  background-color: white;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  box-shadow: 0 13px 25px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.Runtime {
    font-weight: 700;
  background-color: white;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  box-shadow: 0 13px 25px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.Rating {
    font-weight: 700;
  background-color: white;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  box-shadow: 0 13px 25px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.Description {
  margin: auto;
  width: 70%;
  border-radius: 10px;
  background-color: white;
}
