.Container {
    display: flex;
    justify-content: center;
  }
  
  .Movies {
      display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
  }

  .Loading {
    display: flex;
    text-align: center;
    margin-top: 600px;
    font-size: xx-large;
    color: rgb(129, 135, 141);
  }
  
  