html {
  background-color: rgb(236, 247, 255);
  height: 100%;
}

.movie {
  background-color: rgb(255, 255, 255);
  padding: 20px;
  border-radius: 5px;
  display: grid;
  grid-gap: 15px;
  text-decoration: none;
  box-shadow: 0 13px 25px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movieContainer {
  margin-top: 50px;
  display: flex;
  height: 100%;
  justify-content: center;
}
img {
  position: relative;
  top: -50px;
  width: 100%;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movieimg {
  margin-left: 30px;
  margin-right: 30px;
  max-width: 150px;
  max-height: 240px;
}
.movieList {
  background-color: rgb(255, 255, 255);
  display: flex;
}

.movieInformation {
  margin-left: 10px;
}
.movieTitle {
  text-decoration: none;
}
.movieInformation p {
  font-size: 200;
}

.movieTitle a {
  font-weight: 700;
  margin-bottom: 5px;
  font-size: 24px;
  color: rgb(35, 45, 101);
  text-decoration: none;
}

.movieYear {
  position: relative;
  top: -15px;
  color: rgb(151, 149, 149);
  margin-bottom: 10px;
}
.movieRating {
  position: relative;
  top: -15px;
  color: rgb(151, 149, 149);
}

.movieGenres {
  color: rgb(151, 149, 149);
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.movieGenres li {
  margin-right: 10px;
  font-size: 14px;
}
